@import '../../assets/css/theme';

.sun-action-btns {
  button {
    border-radius: 10px;
    border: none;
    &.single {
      width: 100%;
      background: $color-button-primary;
      border-radius: 10px;
      color: $white;
      cursor: pointer;
      display: block;
      margin: 0 auto;
      font-size: 16px;
      height: 56px;
      border-radius: 10px;
      font-family: Helvetica-Bold, Helvetica;
      text-align: center;

      &.single-disabled {
        background: $color-button-primary;
        opacity: 0.3;
        cursor: default;
      }
      &.gray {
        color: $font999;
        &:hover {
          background: nth($gray, 2);
        }

        background: nth($gray, 2);
      }

      &.half {
        width: 49%;
        max-width: 260px;
        margin-left: 2%;

        &:first-child {
          margin-left: 0;
        }
      }

      &.trisection {
        width: 32%;
        max-width: 160px;
        margin-left: 25px;

        &:first-child {
          margin-left: 0;
        }
      }

      &.blue {
        background: $color-button-primary;
        color: #fff;
        &:hover {
          background: $color-button-primary;
        }
      }

      &.orange {
        background: $orange;
        color: #fff;
      }
    }
  }
  button {
    &.schedule-btn {
      margin-right: 33px;
      width: 100px;
      height: 40px;
      line-height: 40px;
      border-radius: 10px;
      background: rgba($color: $color-button-primary, $alpha: 0.2);
      color: $color-button-primary;
      font-size: 16px;
      font-family: AvenirNext-Medium;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        background-color: $hoverPurple;
      }
    }

    &.schedule-btn-active {
      background: $color-button-primary;
      height: 40px;
      line-height: 40px;
      color: #fff;
    }

    &.half {
      width: 49%;
      max-width: 260px;
      margin-left: 2%;

      &:first-child {
        margin-left: 0;
      }
    }

    &.trisection {
      width: 32%;
      max-width: 160px;
      margin-left: 25px;

      &:first-child {
        margin-left: 0;
      }
    }

    &.gray {
      cursor: auto;
      color: $white;
      background: nth($gray, 2);
    }

    &.blue {
      background: $color-button-primary;
      color: #fff;
      position: relative;

      &:hover {
        background: $hoverBlue;
      }

      .ques {
        position: absolute;
        top: 26px;
        right: 26px;
      }
    }

    &.purple {
      background: rgba($color: $color-button-primary, $alpha: 0.2);
      color: $color-button-primary;
    }

    &.green {
      background: rgba($color: $green, $alpha: 0.2);
      color: $green;
    }

    &.ant-pagination-item-link {
      height: 32px;
      line-height: 32px;
    }

    &.orange {
      background: $warning;
      color: #fff;
    }

    &.red {
      background: $error !important;
      color: #fff !important;
    }
  }
}
@media screen and (max-width: 750px) {
  .sun-action-btns {
    button.single {
      padding: 10px 0;
      font-size: 16px;
    }
  }
}
